export const flowers = [{ "name": "evergreen candytuft", "genus": "Iberis", "colors": ["white"], "petals": "many", "edge": ["rounded square"], "size": "small", "shape": "cluster", "tree": "no", "description": "Candytuft! What a delightful name for a delightful flower. There is something immensely satisfying (but also frightening) about a cluster of flowers so thick you can't see any green. ", "new": false }, { "name": "scarlet globemallow", "genus": "Sphaeralcea", "colors": ["orange"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "bowl", "tree": "no", "description": "Not totally scarlet, globular, or mallow-y, this nonetheless pleasant wildflower is also known as \"cowboy's delight.\" In late spring you can see large patches of these on drier hills in Utah, whether or not you're on horseback.", "new": false }, { "name": "spreading phlox", "genus": "Phlox", "colors": ["pink"], "petals": "5", "edge": ["round", "rounded square"], "size": "small", "shape": "flat", "tree": "no", "description": "These flowers really do spread\u2014you can catch them in enormous patches in shady areas pretty easily, because they can't run.", "new": false }, { "name": "Sarah Bernhardt peony", "genus": "Paeonia", "colors": ["pink"], "petals": "many", "edge": ["notched", "ruffled"], "size": "large", "shape": "carnation-like", "tree": "no", "description": "This enormous, gorgeous flower is like a giant cross between a carnation and a rose.", "new": false }, { "name": "showy milkweed", "genus": "Asclepias", "colors": ["pink"], "petals": "5", "edge": ["pointy"], "size": "large", "shape": "ball", "tree": "no", "description": "As far as I can tell, the show this flower's putting on is called \"Ball of starfish.\"", "new": false }, { "name": "tapertip onion", "genus": "Allium", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "ball", "tree": "no", "description": "Also called Hooker's onion, this plant is allegedly quite delicious. ", "new": false }, { "name": "Colorado blue columbine", "genus": "Aquilegia", "colors": ["white"], "petals": "5", "edge": ["round"], "size": "medium", "shape": "star", "tree": "no", "description": "I'm not completely sure that I've identified these flowers correctly, but I do know they're columbines\u2014you can tell because they have funky little tails coming out the back (called \"spurs\").", "new": false }, { "name": "smoothstem blazingstar", "genus": "Mentzelia", "colors": ["yellow"], "petals": "5", "edge": ["pointy"], "size": "large", "shape": "star", "tree": "no", "description": "This is one of my favorite wildflowers. I love its extravagant name! In early summer, you can find small shrubs in bloom with these highlighter-yellow flowers\u2014act quick, though, because those shrubs will quickly dry out into sad skeletons.", "new": false }, { "name": "chicory", "genus": "Cichorium", "colors": ["purple"], "petals": "many", "edge": ["notched"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "This elusive purple-blue wildflower is a joy to spot! It's rather hard to pick off individual flowers, so I might recommend stealing the entire bush.", "new": false }, { "name": "natal plum", "genus": "Carissa", "colors": ["white"], "petals": "5", "edge": ["round"], "size": "medium", "shape": "star", "tree": "no", "description": "These elegant stars are a perfect fit for beachside stroll aesthetics.", "new": false }, { "name": "purple morning glory", "genus": "Ipomoea", "colors": ["purple"], "petals": "5", "edge": ["notched"], "size": "medium", "shape": "trumpet", "tree": "no", "description": "These are much more eye-catching than the pale pink morning glory that is slowly eating away my hometown.", "new": false }, { "name": "Texas lantana", "genus": "Lantana", "colors": ["yellow", "orange"], "petals": "4", "edge": ["notched", "round"], "size": "small", "shape": "cluster", "tree": "no", "description": "Lantana is a common choice for city-street flowerpots. I love its colorful clusters, but I'm still not sure how many petals it has.", "new": false }, { "name": "lion's tail", "genus": "Leonotis", "colors": ["orange"], "petals": "many", "edge": ["fuzzy"], "size": "small", "shape": "cluster", "tree": "no", "description": "This flower is also called \"wild dagga,\" which sounds like a useful survival tool.", "new": false }, { "name": "globe amaranth", "genus": "Gomphrena", "colors": ["pink", "purple"], "petals": "many", "edge": ["pointy", "notched"], "size": "small", "shape": "ball", "tree": "no", "description": "Wow! These vibrant blooms are extra cool when you look at them close up. At some point I'll add support for multiple photos on this site so you can have that experience.", "new": false }, { "name": "balloon flower", "genus": "Platycodon", "colors": ["purple"], "petals": "5", "edge": ["pointy"], "size": "large", "shape": "star", "tree": "no", "description": "Strangely, these might be the least balloon-looking flowers I've ever seen.", "new": false }, { "name": "toad lily", "genus": "Tricyrtis", "colors": ["white", "purple"], "petals": "6", "edge": ["round"], "size": "small", "shape": "star", "tree": "no", "description": "I love these funky gems! They are NOTHING like toads!", "new": false }, { "name": "dahlia", "genus": "Dahlia", "colors": ["pink"], "petals": "many", "edge": ["round", "notched"], "size": "large", "shape": "ball", "tree": "no", "description": "A classy, easily recognizable flower with a name you could comfortably use on your kid.", "new": false }, { "name": "fire vine", "genus": "Ipomoea", "colors": ["red", "orange", "yellow"], "petals": "5", "edge": ["none"], "size": "small", "shape": "stalk", "tree": "no", "description": "Also known as Spanish flag and firecracker vine, this silly flower brings to mind a pile of happy toucans!", "new": false }, { "name": "bluemink", "genus": "Ageratum", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "I'm a huge fan of this flower's name, which seems like it came from a lazy guy trying to get out of his hunting duties back in the days when the color purple hadn't been invented yet. \"I'm gonna go look for blue mink!\" or something. You can also call it \"flossflower.\"", "new": false }, { "name": "crested cockscomb", "genus": "Celosia", "colors": ["red", "pink"], "petals": "many", "edge": ["ruffled"], "size": "large", "shape": "ruffle", "tree": "no", "description": "I'm a little upset by this one because it's hard to categorize and looks way too much like a brain.", "new": false }, { "name": "white campion", "genus": "Silene", "colors": ["white"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "gourd", "tree": "no", "description": "When I look at these I imagine the taste of wintermelon, a vegetable I hate.", "new": false }, { "name": "lanceleaf liveforever", "genus": "Dudleya", "colors": ["red", "orange", "yellow"], "petals": "6", "edge": ["pointy"], "size": "small", "shape": "stalk", "tree": "no", "description": "I think everything to know about this flower is included in its absurdly original name, the most flagrant offender I've seen of flower-namer-people Just Making Up Words since \"smoothstem blazingstar.\"", "new": false }, { "name": "seafoam statice", "genus": "Limonium", "colors": ["purple", "white"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "cluster", "tree": "no", "description": "A lovely cluster also known as Perez's sea lavender\u2014take a moment to spot the intermittent little white flowers!", "new": false }, { "name": "verbena", "genus": "Verbena", "colors": ["purple"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "cluster", "tree": "no", "description": "It's a ball of flowers, hiding a fuzzy little secret...", "new": false }, { "name": "painter's palette", "genus": "Anthurium", "colors": ["red", "yellow"], "petals": "2", "edge": ["fused"], "size": "large", "shape": "leaf", "tree": "no", "description": "A leaf disguised as a flower, this thing is also called flamingo flower, oilcloth flower, and laceleaf.", "new": false }, { "name": "Texas bluebonnet", "genus": "Lupinus", "colors": ["purple", "yellow", "white"], "petals": "many", "edge": ["round"], "size": "large", "shape": "stalk", "tree": "no", "description": "This photo was taken just off of a highway in Texas in the springtime, just like Lady Bird Johnson wanted. ", "new": false }, { "name": "evening primrose", "genus": "Oenothera", "colors": ["white", "pink"], "petals": "4", "edge": ["notched"], "size": "large", "shape": "bag", "tree": "no", "description": "I classify the shape of this flower as \"bag\" because no flower has looked more like a discarded plastic bag to me. These flowers have no integrity\u2014this photo was taken at 8AM, a far cry from \"evening.\"", "new": false }, { "name": "sego lily", "genus": "Calochortus", "colors": ["white"], "petals": "3", "edge": ["pointy"], "size": "small", "shape": "cup", "tree": "no", "description": "Utah's state flower is much smaller than you might expect. Look closely off-trail in late spring to spot it, and be careful up close\u2014sego lilies make good homes for spiders and other yucky bugs.", "new": false }, { "name": "spider milkweed", "genus": "Asclepias", "colors": ["green", "purple"], "petals": "5", "edge": ["pointy"], "size": "large", "shape": "ball", "tree": "no", "description": "Also called antelope horns milkweed, this flower is absolutely bonkers.", "new": false }, { "name": "narrow-leaf penstemon", "genus": "Penstemon", "colors": ["purple"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "stalk", "tree": "no", "description": "You can also call this plant toad-flax beardtongue\u2014I'm not sure why.", "new": false }, { "name": "thistle", "genus": "Cirsium", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "gourd", "tree": "no", "description": "This thistle is hosting a soldier beetle friend!", "new": false }, { "name": "Lotus japonicus", "genus": "Lotus", "colors": ["yellow"], "petals": "2", "edge": ["round", "pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "To my dismay, this cutie has no common name yet. Sunshine horseshoeflower, anyone?", "new": false }, { "name": "musk-mallow", "genus": "Malva", "colors": ["pink"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "bowl", "tree": "no", "description": "After looking at all these flowers called \"mallow,\" I'm really curious who was responsible for naming marshmallows.", "new": false }, { "name": "Queen Anne's lace", "genus": "Daucus", "colors": ["white"], "petals": "many", "edge": ["round"], "size": "large", "shape": "cluster", "tree": "no", "description": "Alternative names for this plant include wild carrot and bird's nest, neither of which evoke as fun of an image as Queen Anne's lace. If you look closely you can see a red dot in the center of the flower cluster that represents blood from Queen Anne pricking her finger while making the lace, which is pretty disgusting if you ask me.", "new": false }, { "name": "bird vetch", "genus": "Vicia", "colors": ["purple"], "petals": "2", "edge": ["round", "fused"], "size": "small", "shape": "stalk", "tree": "no", "description": "This flower has a lot of other arguably descriptive common names such as tufted vetch, boreal vetch, cow vetch, and blue vetch. The only thing the flower community seems to have agreed upon is that it's vetch!", "new": false }, { "name": "red clover", "genus": "Trifolium", "colors": ["pink"], "petals": "many", "edge": ["round", "pointy"], "size": "small", "shape": "ball", "tree": "no", "description": ":)", "new": false }, { "name": "crownvetch", "genus": "Securigera", "colors": ["pink"], "petals": "many", "edge": ["round"], "size": "small", "shape": "cluster", "tree": "no", "description": "I would like to talk to the person who came up with the name vetch, because it sounds much too pointy for flowers!", "new": false }, { "name": "buttonbush", "genus": "Cephalanthus", "colors": ["white", "brown"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "ball", "tree": "no", "description": "Also called honey-bells, this flower was too spiky for this bee to linger long.", "new": false }, { "name": "garden nasturtium", "genus": "Tropaeolum", "colors": ["orange"], "petals": "5", "edge": ["ruffled", "round", "notched"], "size": "medium", "shape": "cup", "tree": "no", "description": "Nasturtium is an appropriate name for a flower that is beautiful but looks like it would gossip if it could.", "new": false }, { "name": "everlasting flower", "genus": "Helichrysum", "colors": ["pink", "white"], "petals": "many", "edge": ["round"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "I love the shiny mysteriousness of this flower! We may never know what it's hiding in its heart.", "new": false }, { "name": "trumpet vine", "genus": "Campsis", "colors": ["orange"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "trumpet", "tree": "no", "description": "The alternative name for this flower, \"cow itch vine,\" raises a lot of questions.", "new": false }, { "name": "desert wirelettuce", "genus": "Stephanomeria", "colors": ["pink"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "star", "tree": "no", "description": "I had a hard time identifying this flower and may have done it wrong\u2014I've thought before that it's skeletonplant. It's got impressive spork petals.", "new": false }, { "name": "narrowleaf four o'clock", "genus": "Mirabilis", "colors": ["white", "pink"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "trumpet", "tree": "no", "description": "Four o'clocks were thusly named because they open at four o'clock, and I guess the people who named them had never been up at 7AM like I have.", "new": false }, { "name": "Egyptian starcluster", "genus": "Pentas", "colors": ["pink"], "petals": "5", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "The color of these flowers does not evoke Egypt for me. Instead, I'm thinking about a shirt I thought was cool in 2014...", "new": false }, { "name": "Soprano Lilac Spoon African daisy", "genus": "Osteospermum", "colors": ["white", "pink"], "petals": "many", "edge": ["spoon"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "These are some of the mesmerizingest flowers I've spotted. Also called Cape marguerite, their petals look like, but cannot be used as, spoons.", "new": false }, { "name": "nemesia", "genus": "Nemesia", "colors": ["pink", "yellow"], "petals": "many", "edge": ["round", "notched"], "size": "medium", "shape": "cluster", "tree": "no", "description": "Tutu designers could learn a thing or two from these flowers!", "new": false }, { "name": "spider flower", "genus": "Grevillea", "colors": ["red", "green"], "petals": "many", "edge": ["round", "pointy"], "size": "small", "shape": "ball", "tree": "no", "description": "I'd have named this anything other than spider flower.", "new": false }, { "name": "blue felicia", "genus": "Felicia", "colors": ["purple"], "petals": "many", "edge": ["round"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "This flower is another relic from the days before purple was invented.", "new": false }, { "name": "tiger-eye pansy", "genus": "Viola", "colors": ["yellow", "brown"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "flat", "tree": "no", "description": "According to Wikipedia, pansies are hybrid creations, like mules.", "new": false }, { "name": "anemone", "genus": "Anemone", "colors": ["white", "pink", "red", "purple"], "petals": "many", "edge": ["round", "rounded square"], "size": "large", "shape": "bowl", "tree": "no", "description": "Sea anemones were named after anemone flowers because both are colorful, which is hilarious because I can think of plenty of flowers that are just as colorful AND easier to spell.", "new": false }, { "name": "common stork's-bill", "genus": "Erodium", "colors": ["pink"], "petals": "5", "edge": ["round", "rounded square"], "size": "small", "shape": "star", "tree": "no", "description": "Also called redstem filaree and pinweed, this flower is known as stork's bill because it's got sharp, beak-looking seed-pods.", "new": false }, { "name": "birdeye speedwell", "genus": "Veronica", "colors": ["blue"], "petals": "4", "edge": ["round"], "size": "small", "shape": "bowl", "tree": "no", "description": "Other names for this plant include winter speedwell and bird's-eye. ", "new": false }, { "name": "dandelion", "genus": "Taraxacum", "colors": ["yellow"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "circle", "tree": "no", "description": "This one has a red soldier bug on it!", "new": false }, { "name": "grape hyacinth", "genus": "Muscari", "colors": ["purple"], "petals": "1", "edge": ["round", "notched"], "size": "small", "shape": "stalk", "tree": "no", "description": "Neither hyacinth nor grapes.", "new": false }, { "name": "apple", "genus": "Malus", "colors": ["white"], "petals": "5", "edge": ["round", "ruffled"], "size": "small", "shape": "bowl", "tree": "yes", "description": "I adore the smell of this flower! Much better than the fishy smell of the Bradford pear.", "new": false }, { "name": "grass lily", "genus": "Ornithogalum", "colors": ["white"], "petals": "6", "edge": ["pointy"], "size": "small", "shape": "cup", "tree": "no", "description": "Also called nap-at-noon and eleven-o'clock lady, these little lilies were a stellar surprise to find on the side of a trail. ", "new": false }, { "name": "Japanese wisteria", "genus": "Wisteria", "colors": ["white", "purple"], "petals": "2", "edge": ["fused", "pointy", "round"], "size": "large", "shape": "stalk", "tree": "no", "description": "I'm not actually sure if this one is a tree.", "new": false }, { "name": "Kanzan cherry", "genus": "Prunus", "colors": ["pink"], "petals": "many", "edge": ["notched", "ruffled"], "size": "medium", "shape": "carnation-like", "tree": "yes", "description": "In my humble opinion, the best color of tree is pink!", "new": false }, { "name": "Chinese redbud", "genus": "Cercis", "colors": ["pink"], "petals": "5", "edge": ["round", "fused"], "size": "small", "shape": "cluster", "tree": "yes", "description": "With fiercely magenta flowers, these trees make a powerful contribution to any springtime neighborhood.", "new": false }, { "name": "lilac", "genus": "Syringa", "colors": ["white", "pink"], "petals": "4", "edge": ["round"], "size": "small", "shape": "stalk", "tree": "no", "description": "An unassuming shrub in most seasons, the lilac is a wonderful surprise in the spring.", "new": false }, { "name": "Pursh's milkvetch", "genus": "Astragalus", "colors": ["pink", "purple"], "petals": "4", "edge": ["round", "ruffled", "fused"], "size": "medium", "shape": "cluster", "tree": "no", "description": "So strangely shaped my janky classification system struggles to describe it, Pursh's milkvetch exists to push the boundaries of what a flower is. Why do the flowers come in threes (or maybe fours\u2014is that a bud in the photo)? Why are they so long? What's going on with the petals?", "new": false }, { "name": "meadow death camas", "genus": "Toxicoscordion", "colors": ["white", "yellow"], "petals": "5", "edge": ["ruffled", "round"], "size": "small", "shape": "stalk", "tree": "no", "description": "If you eat 2-6% of your body weight in meadow death camas, you'll probably die.", "new": false }, { "name": "milkvetch", "genus": "Astragalus", "colors": ["white", "yellow"], "petals": "4", "edge": ["round", "notched", "fused"], "size": "small", "shape": "cluster", "tree": "no", "description": "I'm not sure what variety of milkvetch this is\u2014the other yellow-white milkvetches I found, on top of not having the same leaves as this one, tended to be native to an extremely specific region beyond where I found this. (Which is why we should have more dichotomous keys!)", "new": false }, { "name": "firewheel", "genus": "Gaillardia", "colors": ["red", "yellow"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "This sandy beauty is also known as Indian blanket, Indian blanketflower, or sundance. Its Wikipedia page contains more instances of the word \"hairy\" than one might expect.", "new": false }, { "name": "society garlic", "genus": "Tulbaghia", "colors": ["purple"], "petals": "6", "edge": ["round", "pointy"], "size": "medium", "shape": "cluster", "tree": "no", "description": "I adore the name society garlic, and its contrast to this flower's phlegmy genus name, Tulbaghia. You can also call this cluster pink agapanthus, which is silly because it's not pink, and \"spring flowers,\" which is laughably generic.", "new": false }, { "name": "oleander", "genus": "Nerium", "colors": ["red"], "petals": "5", "edge": ["rounded square"], "size": "medium", "shape": "flat", "tree": "no", "description": "A more accurate name for the shape of these might be \"twisty star.\"", "new": false }, { "name": "yellow hibiscus", "genus": "Hibiscus", "colors": ["yellow"], "petals": "5", "edge": ["notched", "ruffled"], "size": "large", "shape": "trumpet", "tree": "no", "description": "Growing up, my dad would always call hibiscus \"Rose of Sharon,\" which I've just now learned is a biblical phrase about some sort of flower that might not even be hibiscus.", "new": false }, { "name": "baby sun rose", "genus": "Mesembryanthemum", "colors": ["yellow", "pink"], "petals": "many", "edge": ["round", "pointy"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "Also called aptenia and heart-leaf, these little friends are a pleasure to catch opening when the sun's out.", "new": false }, { "name": "shoeblackplant", "genus": "Hibiscus", "colors": ["pink", "red", "yellow"], "petals": "5", "edge": ["notched", "ruffled"], "size": "large", "shape": "trumpet", "tree": "no", "description": "It's just a hibiscus, but for some reason we're calling it shoeblackplant.", "new": false }, { "name": "cobblers pegs", "genus": "Bidens", "colors": ["white", "yellow"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "star", "tree": "no", "description": "It's so hard to choose a favorite among this flower's common names, which include beggarticks, black-jack, and farmer's friends.", "new": false }, { "name": "daisy fleabane", "genus": "Erigeron", "colors": ["white", "yellow"], "petals": "many", "edge": ["round", "pointy"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "Tiny little flowers!", "new": false }, { "name": "cape leadwort", "genus": "Plumbago", "colors": ["blue", "purple"], "petals": "5", "edge": ["round", "notched"], "size": "medium", "shape": "cluster", "tree": "no", "description": "You can also call this stuff blue plumbago.", "new": false }, { "name": "Virginia buttonweed", "genus": "Diodia", "colors": ["white"], "petals": "4", "edge": ["pointy"], "size": "small", "shape": "star", "tree": "no", "description": "According to Wikipedia, this stuff can be a \"nuisance weed,\" but nonetheless, it makes my list of Things That Are Cooler To Use On Lawns Than Grass.", "new": false }, { "name": "southern magnolia", "genus": "Magnolia", "colors": ["white"], "petals": "many", "edge": ["round", "pointy"], "size": "large", "shape": "bowl", "tree": "yes", "description": "A humongous, superb flower. I'm upset that not all trees do this.", "new": false }, { "name": "waterhyssop", "genus": "Bacopa", "colors": ["white", "blue"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "cup", "tree": "no", "description": "These teeny guys can also be called things like thyme-leafed gratiola and herb of grace.", "new": false }, { "name": "seashore mallow", "genus": "Kosteletzkya", "colors": ["pink", "yellow"], "petals": "5", "edge": ["round", "notched"], "size": "medium", "shape": "star", "tree": "no", "description": "I can't believe it's not hibiscus!", "new": false }, { "name": "purple arrowroot", "genus": "Canna", "colors": ["orange"], "petals": "many", "edge": ["round", "ruffled"], "size": "large", "shape": "stalk", "tree": "no", "description": "This plant is also known as Indian shot, African arrowroot, Sierra Leone arrowroot, and edible canna. I chose to use the name \"purple arrowroot\" because I think it's hilarious that the person who named it just didn't want to talk about the giant orange flowers.", "new": false }, { "name": "cutleaf evening primrose", "genus": "Oenothera", "colors": ["pink", "yellow", "orange"], "petals": "many", "edge": ["round", "ruffled"], "size": "small", "shape": "bag", "tree": "no", "description": "I refuse to call the shape of primroses anything but \"bag.\"", "new": false }, { "name": "white oleander", "genus": "Nerium", "colors": ["white"], "petals": "5", "edge": ["rounded square"], "size": "medium", "shape": "flat", "tree": "no", "description": "Ooh ooh! Oleander, but white! ", "new": false }, { "name": "African lily", "genus": "Agapanthus", "colors": ["purple"], "petals": "many", "edge": ["round", "pointy"], "size": "large", "shape": "ball", "tree": "no", "description": "These happy blooms are also called lily of the Nile.", "new": false }, { "name": "scarlet bottlebrush", "genus": "Melaleuca", "colors": ["red"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "ball", "tree": "no", "description": "An excellently named, brilliantly executed ball of cheer. I think this is what pom-poms and fireworks should be.", "new": false }, { "name": "spider hibiscus", "genus": "Hibiscus", "colors": ["red"], "petals": "many", "edge": ["notched", "round"], "size": "medium", "shape": "ball", "tree": "no", "description": "Fantastic! More flowers should be lanterns.", "new": false }, { "name": "peacock ginger", "genus": "Kaempferia", "colors": ["purple", "white"], "petals": "4", "edge": ["round"], "size": "small", "shape": "flat", "tree": "no", "description": "At some point I might write up a little flower-to-name matching game that everyone will lose because this friend looks nothing like what comes to mind when anyone says \"peacock ginger.\"", "new": false }, { "name": "nipplewort", "genus": "Lapsana", "colors": ["yellow"], "petals": "many", "edge": ["notched"], "size": "small", "shape": "cluster", "tree": "no", "description": "Clearly an invasive species\u2014who would want a nipple-wart?", "new": false }, { "name": "fire-star orchid", "genus": "Epidendrum", "colors": ["orange", "red"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "cluster", "tree": "no", "description": "Five stars for fire stars!", "new": false }, { "name": "weeping blue ginger", "genus": "Dichorisandra", "colors": ["purple", "white"], "petals": "3", "edge": ["ruffled", "notched", "round"], "size": "medium", "shape": "stalk", "tree": "no", "description": "Weeping is an appropriate response when encountering flowers named by that one guy who never learned the word \"purple.\"", "new": false }, { "name": "Greve's orchid tree", "genus": "Bauhinia", "colors": ["yellow", "red"], "petals": "5", "edge": ["round", "ruffled", "notched"], "size": "medium", "shape": "bowl", "tree": "yes", "description": "This thing is trying very hard to be a flower, and not succeeding at all, but I guess we can call it a flower anyway.", "new": false }, { "name": "bush lily", "genus": "Clivia", "colors": ["yellow", "white", "red"], "petals": "6", "edge": ["pointy", "round"], "size": "large", "shape": "cluster", "tree": "no", "description": "Another fabulous instance of nature making bouquets for us.", "new": false }, { "name": "walking iris", "genus": "Trimezia", "colors": ["yellow", "brown"], "petals": "3", "edge": ["round", "notched"], "size": "medium", "shape": "iris-like", "tree": "no", "description": "These plants \"walk\" by sprouting in a line. Their coloration is very banana-like!", "new": false }, { "name": "Dalmation Peach foxglove", "genus": "Digitalis", "colors": ["pink"], "petals": "4", "edge": ["fused", "round"], "size": "medium", "shape": "stalk", "tree": "no", "description": "This plant, despite looking quite friendly, is made of poison.", "new": false }, { "name": "daisy", "genus": "Gerbera", "colors": ["yellow"], "petals": "many", "edge": ["round", "pointy", "notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "Daisies are one of the most used cut flowers in the world! They are perfect.", "new": false }, { "name": "fairy fan-flower", "genus": "Scaevola", "colors": ["purple"], "petals": "5", "edge": ["ruffled", "pointy"], "size": "medium", "shape": "cluster", "tree": "no", "description": "It's fair to be a fan of the fairy fan-flower.", "new": false }, { "name": "purple passionflower", "genus": "Passiflora", "colors": ["purple", "white"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "circle", "tree": "no", "description": "This thing is nuts! It's got everything! It's got little bone-looking structures, a polka-dot star, and funky wiggles! You can even call it maypop or wild apricot if you want.", "new": false }, { "name": "cornflower", "genus": "Centaurea", "colors": ["blue", "purple"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "circle", "tree": "no", "description": "I find this flower beautiful and its alternate name, \"bachelor's button,\" immensely satisfying.", "new": false }, { "name": "Aztec marigold", "genus": "Tagetes", "colors": ["orange"], "petals": "many", "edge": ["ruffled"], "size": "medium", "shape": "circle", "tree": "no", "description": "Ruffle fluffle! A great flower.", "new": false }, { "name": "calibrachoa", "genus": "Calibrachoa", "colors": ["pink", "white", "yellow"], "petals": "5", "edge": ["pointy", "fused"], "size": "small", "shape": "trumpet", "tree": "no", "description": "These brightly colored friends are often mistaken for petunias.", "new": false }, { "name": "marigold", "genus": "Tagetes", "colors": ["orange", "yellow", "red"], "petals": "6", "edge": ["ruffled", "notched", "round"], "size": "medium", "shape": "flat", "tree": "no", "description": "A happy group of blooms!", "new": false }, { "name": "purple iris", "genus": "Iris", "colors": ["purple", "yellow", "white"], "petals": "6", "edge": ["round", "ruffled"], "size": "medium", "shape": "iris-like", "tree": "no", "description": "This gentleman is also known as poison flag and northern blue flag, but I chose purple iris because I think it's the most straightforward. ", "new": false }, { "name": "blue-eyed grass", "genus": "Sisyrinchium", "colors": ["blue", "yellow"], "petals": "6", "edge": ["pointy"], "size": "small", "shape": "star", "tree": "no", "description": "I've never seen grass with eyes before!", "new": false }, { "name": "Amur honeysuckle", "genus": "Lonicera", "colors": ["white", "yellow"], "petals": "5", "edge": ["rounded square", "fused"], "size": "medium", "shape": "funky", "tree": "no", "description": "Silly as it is, this plant is unpleasantly invasive.", "new": false }, { "name": "black locust", "genus": "Robinia", "colors": ["white", "yellow"], "petals": "5", "edge": ["fused", "round"], "size": "large", "shape": "funky", "tree": "yes", "description": "I think it's rude that a flower and a bug have the same name.", "new": false }, { "name": "false spikenard", "genus": "Maianthemum", "colors": ["white"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "stalk", "tree": "no", "description": "You can call this stuff treacleberry, feathery false lily of the valley, or Solomon's plume, but I think false spikenard is best because it cracks me up.", "new": false }, { "name": "water forget-me-not", "genus": "Myosotis", "colors": ["blue"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "star", "tree": "no", "description": "Wow wow wow! These are a delight to stumble upon.", "new": false }, { "name": "golden alexander", "genus": "Zizia", "colors": ["yellow"], "petals": "many", "edge": ["round"], "size": "medium", "shape": "cluster", "tree": "no", "description": "It's like Queen Anne's lace, but better.", "new": false }, { "name": "crown of thorns", "genus": "Euphorbia", "colors": ["pink"], "petals": "2", "edge": ["round", "notched"], "size": "small", "shape": "cluster", "tree": "no", "description": "The most Pepto-Bismol flower I've seen!", "new": false }, { "name": "peregrina", "genus": "Jatropha", "colors": ["red"], "petals": "5", "edge": ["round", "pointy"], "size": "small", "shape": "star", "tree": "no", "description": "I think this is peregrina (also known as spicy jatropha), but I'm not too confident in that identification.", "new": false }, { "name": "pincushion flower", "genus": "Scabiosa", "colors": ["purple"], "petals": "many", "edge": ["round", "notched"], "size": "small", "shape": "circle", "tree": "no", "description": "What a lovely flower! Pincushion is a much better name for it than its other common one, \"small scabious.\"", "new": false }, { "name": "climbing hydrangea", "genus": "Hydrangea", "colors": ["white"], "petals": "4", "edge": ["round"], "size": "large", "shape": "cluster", "tree": "no", "description": "This one is so silly! I saw an entire wall of it. It's kinda like if everyone on one of those spinny carnival rides stuck their legs out.", "new": false }, { "name": "peach verbena", "genus": "Verbena", "colors": ["red", "orange", "pink"], "petals": "5", "edge": ["notched", "ruffled"], "size": "small", "shape": "cluster", "tree": "no", "description": "I had to call out this particular variety of verbena because it's beautiful.", "new": false }, { "name": "valerian", "genus": "Valeriana", "colors": ["white", "pink"], "petals": "5", "edge": ["round"], "size": "small", "shape": "cluster", "tree": "no", "description": "I'm pretty sure the plant came before the TV show.", "new": false }, { "name": "Festiva Maxima peony", "genus": "Paeonia", "colors": ["white", "red"], "petals": "many", "edge": ["ruffled", "notched"], "size": "large", "shape": "carnation-like", "tree": "no", "description": "Peonies are top-notch!", "new": false }, { "name": "wild thyme", "genus": "Thymus", "colors": ["purple"], "petals": "4", "edge": ["round", "rounded square"], "size": "small", "shape": "cluster", "tree": "no", "description": "If you eat this, you can say you had a wild thyme.", "new": false }, { "name": "bloody geranium", "genus": "Geranium", "colors": ["pink"], "petals": "5", "edge": ["ruffled"], "size": "medium", "shape": "bowl", "tree": "no", "description": "You can also call geranium cranesbill because it's got pointy seed pods that look like crane bills, but function quite differently.", "new": false }, { "name": "chives", "genus": "Allium", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "ball", "tree": "no", "description": "I love chives! Wow! I have one named Clive who is currently in the care of a lovely lady named Heidi in upstate New York.", "new": false }, { "name": "common sage", "genus": "Salvia", "colors": ["purple"], "petals": "2", "edge": ["fused"], "size": "medium", "shape": "stalk", "tree": "no", "description": "At some point I'm going to have to figure out what the official name for this flower shape is.", "new": false }, { "name": "dame's rocket", "genus": "Hesperis", "colors": ["purple"], "petals": "4", "edge": ["notched"], "size": "medium", "shape": "cluster", "tree": "no", "description": "It's so hard to choose a favorite common name for this one! I also like \"night-scented gilliflower,\" \"mother-of-the-evening,\" and \"Good & Plenties.\"", "new": false }, { "name": "Persian onion", "genus": "Allium", "colors": ["purple"], "petals": "5", "edge": ["pointy"], "size": "large", "shape": "ball", "tree": "no", "description": "I think the rule is: purple ball flower = onion.", "new": false }, { "name": "Serenity Pink Magic African daisy", "genus": "Osteospermum", "colors": ["pink"], "petals": "many", "edge": ["round", "notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "These flowers are seriously magical, and they look just as painted in real life. African daisies are such talented plants!", "new": false }, { "name": "purple verbena", "genus": "Verbena", "colors": ["purple", "pink"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "cluster", "tree": "no", "description": "You can also call verbena \"vervain,\" which has a great mouthfeel.", "new": false }, { "name": "pink vein calibrachoa", "genus": "Calibrachoa", "colors": ["pink"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "trumpet", "tree": "no", "description": "I had to include this guy even though I have another calibrachoa\u2014the pink veins are just too cool! And look how the center is full of lovely ruffles!", "new": false }, { "name": "Japanese snowball", "genus": "Viburnum", "colors": ["white"], "petals": "4", "edge": ["notched"], "size": "medium", "shape": "cluster", "tree": "no", "description": "The flower clusters in this particular tree were pretty flat, but you can cultivate varieties of this shrub so the clusters are actually ball-shaped.", "new": false }, { "name": "Virginia spiderwort", "genus": "Tradescantia", "colors": ["purple"], "petals": "3", "edge": ["ruffled"], "size": "small", "shape": "star", "tree": "no", "description": "I don't know about you, but this flower looks nothing like a spider to me.", "new": false }, { "name": "Sicilian honey garlic", "genus": "Allium", "colors": ["green", "purple", "brown"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "bell", "tree": "no", "description": "Also known as Mediterranean bells.", "new": false }, { "name": "yellow pansy", "genus": "Viola", "colors": ["yellow"], "petals": "5", "edge": ["round", "ruffled"], "size": "medium", "shape": "flat", "tree": "no", "description": "Pansy petals remind me of butterfly wings! ", "new": false }, { "name": "coral honeysuckle", "genus": "Lonicera", "colors": ["red", "pink"], "petals": "5", "edge": ["round", "fused"], "size": "medium", "shape": "trumpet", "tree": "no", "description": "Honeysuckle flowers are so different-looking from each other, I'm starting to suspect that we just slap the name \"honeysuckle\" on any funky viny plant. According to Wikipedia, this plant is so flammable it's not recommended you plant it near residences.", "new": false }, { "name": "rhododendron", "genus": "Rhododendron", "colors": ["pink"], "petals": "5", "edge": ["ruffled", "notched", "round"], "size": "medium", "shape": "cluster", "tree": "no", "description": "Did you know all azaleas are rhododendrons, but not all rhododendrons are azaleas? ", "new": false }, { "name": "Miquel's mazus", "genus": "Mazus", "colors": ["purple"], "petals": "4", "edge": ["fused", "notched", "round"], "size": "small", "shape": "funky", "tree": "no", "description": "This species is native to Japan and China, and invasive everywhere else. It's so funky-looking I can almost forgive it.", "new": false }, { "name": "alpine columbine", "genus": "Aquilegia", "colors": ["purple"], "petals": "5", "edge": ["round"], "size": "medium", "shape": "star", "tree": "no", "description": "Columbines are one of the first kinds of flowers I learned to recognize in the wild! It's nice to find a type of flower that is all the same shape (I'm looking at you, clematis).", "new": false }, { "name": "pink honeysuckle", "genus": "Lonicera", "colors": ["red"], "petals": "many", "edge": ["round"], "size": "small", "shape": "bowl", "tree": "no", "description": "It's like a Christmas spider!", "new": false }, { "name": "scrambled eggs", "genus": "Corydalis", "colors": ["yellow", "orange"], "petals": "4", "edge": ["fused"], "size": "small", "shape": "stalk", "tree": "no", "description": "Other names for this flower include golden smoke and golden corydalis, which you could use if for some reason you don't adore the moniker \"scrambled eggs.\"", "new": false }, { "name": "Pink Panther coral bells", "genus": "Heuchera", "colors": ["white", "pink"], "petals": "many", "edge": ["fused"], "size": "small", "shape": "stalk", "tree": "no", "description": "With leaves as cool as these, you barely need flowers. That's probably why these bells are so small!", "new": false }, { "name": "heucherella", "genus": "Heuchera x Tiarella", "colors": ["pink"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "stalk", "tree": "no", "description": "These flowers are a cross between coral bells and foam flowers, so you can call them \"foamy bells.\"", "new": false }, { "name": "kousa dogwood", "genus": "Cornus", "colors": ["white", "green"], "petals": "4", "edge": ["pointy"], "size": "medium", "shape": "star", "tree": "yes", "description": "You can also call kousa dogwood \"strawberry tree,\" presumably because its fruits are red. (Red spikeballs, that is. It's hard to imagine someone mistaking them for strawberries.)", "new": false }, { "name": "bluestars", "genus": "Amsonia", "colors": ["blue"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "stalk", "tree": "no", "description": "A perfect name for this lovely plant! Finally, a flower named \"blue\" that isn't purple.", "new": false }, { "name": "rose", "genus": "Rosa", "colors": ["orange"], "petals": "many", "edge": ["ruffled", "notched"], "size": "large", "shape": "rose-like", "tree": "no", "description": "This is just an example of a rose. I grew up with only rose bushes separating my house with the neighbors'. It was a favorite summer pastime of mine to spot different colors, but I'm still mad that the purple roses were never on my side of the bush. ", "new": false }, { "name": "Crystal Fountain clematis", "genus": "Clematis", "colors": ["purple", "white"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "star", "tree": "no", "description": "Clematis is absurd! I don't have a picture of hairy clematis yet, but oh boy, you could certainly look it up.", "new": false }, { "name": "purple bloody geranium", "genus": "Geranium", "colors": ["purple"], "petals": "5", "edge": ["notched", "rounded square"], "size": "medium", "shape": "bowl", "tree": "no", "description": "I'm tacking the name \"purple\" onto this plant because I've already got a pink bloody geranium on here.", "new": false }, { "name": "catmint", "genus": "Nepeta", "colors": ["purple"], "petals": "3", "edge": ["fused", "notched"], "size": "medium", "shape": "stalk", "tree": "no", "description": "According to Wikipedia, two-thirds of cats have an intense attraction towards this plant (which I prefer to call catmint instead of catnip).", "new": false }, { "name": "seep monkeyflower", "genus": "Erythranthe", "colors": ["yellow"], "petals": "5", "edge": ["fused", "notched", "round"], "size": "small", "shape": "trumpet", "tree": "no", "description": "You know, these kinda look like monkeys.", "new": false }, { "name": "aspen bluebells", "genus": "Mertensia", "colors": ["blue", "purple"], "petals": "5", "edge": ["round", "ruffled"], "size": "small", "shape": "bell", "tree": "no", "description": "Stunning! These are a pleasure to stumble upon on a dewy morning mountain hike. They're an almost iridescent blue-purple, usually found by water.", "new": false }, { "name": "creeping Oregon grape", "genus": "Mahonia", "colors": ["yellow"], "petals": "many", "edge": ["round", "notched"], "size": "small", "shape": "cluster", "tree": "no", "description": "Apparently I found this plant too soon\u2014later in the season it'll be full of blue berries. The internet tells me those berries are good jelly ingredients.", "new": false }, { "name": "tall western groundsel", "genus": "Senecio", "colors": ["yellow"], "petals": "many", "edge": ["pointy", "notched"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "This flower's other common name, lambstongue ragwort, is pretty funky.", "new": false }, { "name": "western wallflower", "genus": "Erysimum", "colors": ["yellow"], "petals": "4", "edge": ["rounded square"], "size": "medium", "shape": "stalk", "tree": "no", "description": "I get why this is called wallflower (also, prairie rocket). This particular specimen required a bit of rock climbing to get to.", "new": false }, { "name": "ballhead waterleaf", "genus": "Hydrophyllum", "colors": ["white", "purple"], "petals": "5", "edge": ["pointy", "rounded square"], "size": "small", "shape": "ball", "tree": "no", "description": "Waterleaf is such a lovely purple-white color :)", "new": false }, { "name": "serviceberry", "genus": "Amelanchier", "colors": ["white"], "petals": "5", "edge": ["round", "ruffled"], "size": "small", "shape": "cluster", "tree": "no", "description": "You can eat serviceberry fruits! I haven't yet, but I might come back to the pictured shrub later.", "new": false }, { "name": "chokecherry", "genus": "Prunus", "colors": ["white"], "petals": "5", "edge": ["round", "notched"], "size": "medium", "shape": "stalk", "tree": "no", "description": "I love the smell of chokecherries! Unfortunately, Wikipedia says they are pesty\u2014perhaps because they host all sorts of bugs?", "new": false }, { "name": "fernleaf desert parsley", "genus": "Lomatium", "colors": ["yellow"], "petals": "many", "edge": ["round"], "size": "medium", "shape": "cluster", "tree": "no", "description": "It was hard to identify this one\u2014I'm just pretty sure it's a parsley.", "new": false }, { "name": "heartleaf arnica", "genus": "Arnica", "colors": ["yellow"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "What a pleasant surprise to find on a hike!", "new": false }, { "name": "field bindweed", "genus": "Convolvulus", "colors": ["white", "pink"], "petals": "5", "edge": ["ruffled", "rounded square"], "size": "small", "shape": "trumpet", "tree": "no", "description": "These flowers are part of the morning glory family.", "new": false }, { "name": "Lewis flax", "genus": "Linum", "colors": ["blue"], "petals": "5", "edge": ["rounded square"], "size": "small", "shape": "flat", "tree": "no", "description": "I love flax! These lovelies are one of my favorites to find in front gardens.", "new": false }, { "name": "yellow salsify", "genus": "Tragopogon", "colors": ["yellow"], "petals": "many", "edge": ["notched", "round"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "Salsify: a fancy name for a fancy flower. You can also call it goatsbeard, for reasons unknown, or oysterplant.", "new": false }, { "name": "bristly locust", "genus": "Robinia", "colors": ["pink"], "petals": "5", "edge": ["fused", "round"], "size": "large", "shape": "funky", "tree": "yes", "description": "Locusts are such lovely trees!", "new": false }, { "name": "dipelta", "genus": "Dipelta", "colors": ["white"], "petals": "5", "edge": ["round", "ruffled"], "size": "small", "shape": "trumpet", "tree": "no", "description": "This shrub doesn't have a common name yet\u2014a shame! I think we should call it starfishplant.", "new": false }, { "name": "president clematis", "genus": "Clematis", "colors": ["purple"], "petals": "many", "edge": ["pointy", "ruffled"], "size": "large", "shape": "star", "tree": "no", "description": "What the heck! This type of clematis is called \"the President.\"", "new": false }, { "name": "red creeping thyme", "genus": "Thymus", "colors": ["purple"], "petals": "4", "edge": ["round", "notched"], "size": "small", "shape": "flat", "tree": "no", "description": "I saw this filling in the gaps between some paving stones and I love that! Ban grass.", "new": false }, { "name": "moonshine yarrow", "genus": "Achillea", "colors": ["yellow"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "cluster", "tree": "no", "description": "Moonshine yarrow is so bright I bet you could see it from space.", "new": false }, { "name": "yellow avens", "genus": "Geum", "colors": ["yellow"], "petals": "5", "edge": ["round", "ruffled"], "size": "small", "shape": "star", "tree": "no", "description": "This silly thing also comes in a brownish-purple spikeball version! You should see it sometime!", "new": false }, { "name": "California poppy", "genus": "Eschscholzia", "colors": ["orange"], "petals": "4", "edge": ["ruffled", "rounded square"], "size": "medium", "shape": "cup", "tree": "no", "description": "This bright and lovely flower is a pleasure to spot! It's sometimes called cup of gold, which is silly because \"cup of orange\" would be more accurate and more of a tasty idea.", "new": false }, { "name": "red valerian", "genus": "Centranthus", "colors": ["pink"], "petals": "5", "edge": ["round"], "size": "small", "shape": "cluster", "tree": "no", "description": "A pleasant addition to any garden, red valerian has plenty of exciting monikers including devil's beard and kiss-me-quick.", "new": false }, { "name": "lesser periwinkle", "genus": "Vinca", "colors": ["purple"], "petals": "5", "edge": ["rounded square"], "size": "small", "shape": "star", "tree": "no", "description": "This flower is one of my favorites! It's such a fun pinwheel shape. According to Wikipedia, it's sometimes called \"myrtle\" in the United States.", "new": false }, { "name": "snow-in-summer", "genus": "Cerastium", "colors": ["white"], "petals": "5", "edge": ["notched", "round"], "size": "small", "shape": "star", "tree": "no", "description": "Snow-in-summer is a perfect name for a flower. I love this one because of how sneaky it is\u2014you almost don't notice there are flowers against the pale leaves until you look again!", "new": false }, { "name": "coral bells", "genus": "Heuchera", "colors": ["red"], "petals": "5", "edge": ["round", "fused"], "size": "small", "shape": "stalk", "tree": "no", "description": "Just some regular coral bells, a flower from before the word \"red\" was invented.", "new": false }, { "name": "sweet William", "genus": "Dianthus", "colors": ["white"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "flat", "tree": "no", "description": "No one's sure why this flower's called sweet William. It doesn't really look like it wants me to eat it.", "new": false }, { "name": "creeping jenny", "genus": "Lysimachia", "colors": ["yellow"], "petals": "5", "edge": ["notched", "pointy"], "size": "small", "shape": "bowl", "tree": "no", "description": "You can call this plant moneywort, or twopenny grass. Its monetary names come from the \"coin-like\" (I don't really see it) shape of its leaves.", "new": false }, { "name": "spiked speedwell", "genus": "Veronica", "colors": ["purple"], "petals": "4", "edge": ["fused", "round", "ruffled"], "size": "small", "shape": "stalk", "tree": "no", "description": "The name speedwell reminds me of Birdwell, the name of Kronk's love interest in the movie Kronk's New Groove, which I think is a good thing.", "new": false }, { "name": "fuzzy deutzia", "genus": "Deutzia", "colors": ["white"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "stalk", "tree": "no", "description": "This variety of deutzia has double flowers, which either means flowers with extra petals or flowers within flowers! So cool!", "new": false }, { "name": "Scottish bluebell", "genus": "Campanula", "colors": ["purple"], "petals": "5", "edge": ["pointy", "fused"], "size": "small", "shape": "bell", "tree": "no", "description": "Scotland, we might assume, did not invent the word purple for quite some time. (This plant is also known as harebell.)", "new": false }, { "name": "red dahlia", "genus": "Dahlia", "colors": ["red"], "petals": "many", "edge": ["pointy", "notched"], "size": "medium", "shape": "circle", "tree": "no", "description": "According to Wikipedia, dahlias have very nutritious tubers.", "new": false }, { "name": "fringed pink", "genus": "Dianthus", "colors": ["white"], "petals": "5", "edge": ["pointy", "notched"], "size": "small", "shape": "star", "tree": "no", "description": "This stuff is so weird! \"Fringed pink\" also sounds like only half of a name. So does \"large pink,\" the other common name for this flower. Like, what is it that is large and pink? Huh?", "new": false }, { "name": "Starburst chrysanthemum", "genus": "Chrysanthemum", "colors": ["white"], "petals": "many", "edge": ["pointy", "notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "If you zoom way in, you can see a teeny bug on one of these flowers! It might be a cricket or a grasshopper. One of them jumpy things.", "new": false }, { "name": "Nuttall's larkspur", "genus": "Delphinium", "colors": ["blue", "purple"], "petals": "5", "edge": ["pointy", "ruffled"], "size": "medium", "shape": "stalk", "tree": "no", "description": "What a dazzling sight! These beauties are also called two-lobe larkspur, upland larkspur, and common larkspur.", "new": false }, { "name": "petunia", "genus": "Petunia", "colors": ["yellow", "white", "purple"], "petals": "5", "edge": ["ruffled", "fused", "rounded square"], "size": "medium", "shape": "trumpet", "tree": "no", "description": "These are so fun! Also fun fact, the name petunia comes from a South American word for tobacco.", "new": false }, { "name": "lobelia", "genus": "Lobelia", "colors": ["purple"], "petals": "5", "edge": ["pointy", "rounded square"], "size": "small", "shape": "funky", "tree": "no", "description": "It just now occured to me that lobelias are probably called lobelias because they are so lobe-y.", "new": false }, { "name": "feverfew", "genus": "Tanacetum", "colors": ["white"], "petals": "many", "edge": ["rounded square", "notched"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "Whew! This is the kind of stuff you might get at an apothecary: it's called feverfew because it was once thought to reduce fevers.", "new": false }, { "name": "trailing iceplant", "genus": "Lampranthus", "colors": ["purple"], "petals": "many", "edge": ["pointy", "round"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "I love iceplant! Its leaves are so succulent. ", "new": false }, { "name": "poppy", "genus": "Papaver", "colors": ["pink"], "petals": "3", "edge": ["ruffled"], "size": "large", "shape": "bowl", "tree": "no", "description": "I don't know exactly what kind of poppy this is but I had to include it because I got such a happy shot of that wheel-looking thing in its center!", "new": false }, { "name": "Ascot Rainbow spurge", "genus": "Euphorbia x martinii", "colors": ["yellow", "brown"], "petals": "2", "edge": ["round", "fused"], "size": "small", "shape": "cluster", "tree": "no", "description": "If you like this plant, you could splurge on spurge.", "new": false }, { "name": "sacred datura", "genus": "Datura", "colors": ["white"], "petals": "5", "edge": ["fused", "pointy"], "size": "large", "shape": "trumpet", "tree": "no", "description": "This plant can also be called thornapple, after its prickly seed pods. Also, it is very poisonous, which sorta makes sense given that it looks like a monster bindweed.", "new": false }, { "name": "ragwort", "genus": "Senecio", "colors": ["yellow"], "petals": "many", "edge": ["round", "notched"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "The internet is not good at identifying the thousands of flowers that look like this, so ragwort is my best guess.", "new": false }, { "name": "houndstooth", "genus": "Cynoglossum", "colors": ["red", "purple"], "petals": "5", "edge": ["rounded square"], "size": "small", "shape": "cluster", "tree": "no", "description": "This flower has the absolute weirdest common name I've ever heard of. Houndstongue, dog's tongue, and gypsy flower are all par for alternate common names... but you can also call this \"rats and mice.\"", "new": false }, { "name": "camas", "genus": "Camassia", "colors": ["purple", "blue"], "petals": "6", "edge": ["pointy"], "size": "medium", "shape": "stalk", "tree": "no", "description": "My oh my! Camas (also quamash, camash, and wild hyacinth) comes in meadows and its subtle blue-purple is a delight to see. (And don't worry\u2014it's not related to the full-of-poison meadow death camas.)", "new": false }, { "name": "Adam's needle", "genus": "Yucca", "colors": ["white"], "petals": "5", "edge": ["pointy"], "size": "large", "shape": "stalk", "tree": "no", "description": "These hanging flowers are so noticeable! I love how big they are.", "new": false }, { "name": "Japanese meadowsweet", "genus": "Spiraea", "colors": ["pink"], "petals": "5", "edge": ["pointy", "round"], "size": "small", "shape": "cluster", "tree": "no", "description": "If you look closely, these flowers appear to have clipped wires coming out of them.", "new": false }, { "name": "large-leaved lupine", "genus": "Lupinus", "colors": ["purple"], "petals": "4", "edge": ["fused"], "size": "medium", "shape": "stalk", "tree": "no", "description": "Lupine is so fine!", "new": false }, { "name": "two-row stonecrop", "genus": "Phedimus", "colors": ["white", "pink"], "petals": "5", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "This plant can be referred to as \"Caucasian stonecrop,\" which led me to look up where the word \"Caucasian\" comes from\u2014it's the Caucasus, between the Caspian and Black Seas.", "new": false }, { "name": "Pleniflora Japanese kerria", "genus": "Kerria", "colors": ["yellow"], "petals": "many", "edge": ["ruffled"], "size": "small", "shape": "carnation-like", "tree": "no", "description": "My best guess is that this plant is the double-flowered cultivar of Japanese kerria, although the flowers I saw were smaller than Pleniflora is supposed to be.", "new": false }, { "name": "upright prairie coneflower", "genus": "Ratibida", "colors": ["yellow"], "petals": "many", "edge": ["round", "notched"], "size": "medium", "shape": "cone", "tree": "no", "description": "Mexican hat and \"longhead prairie coneflower\" are alternative names for this funky-looking cone with a skirt.", "new": false }, { "name": "goldenrod", "genus": "Solidago", "colors": ["yellow"], "petals": "many", "edge": ["round"], "size": "medium", "shape": "stalk", "tree": "no", "description": "This flower makes me think of the song \"Casimir Pulaski Day\" by Sufjan Stevens.", "new": false }, { "name": "elderberry", "genus": "Sambucus", "colors": ["white"], "petals": "5", "edge": ["round"], "size": "large", "shape": "cluster", "tree": "no", "description": "It's elderberry! It's enormous!", "new": false }, { "name": "Black Lace elderberry", "genus": "Sambucus", "colors": ["white"], "petals": "5", "edge": ["round"], "size": "large", "shape": "cluster", "tree": "no", "description": "I think this variety of elderberry is copyright protected.", "new": false }, { "name": "daphne", "genus": "Daphne", "colors": ["white"], "petals": "4", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "Daphne is a pretty name and a paper shrub, which means it's used to make paper (in this case, mostly in Nepal).", "new": false }, { "name": "Colorado four o'clock", "genus": "Mirabilis", "colors": ["purple"], "petals": "5", "edge": ["round", "fused"], "size": "medium", "shape": "trumpet", "tree": "no", "description": "The color of this flower is in a very tricky place between pink and purple. (Unrelatedly, the powdered root of this plant is allegedly good at reducing appetite.)", "new": false }, { "name": "Paprika yarrow", "genus": "Achillea", "colors": ["red"], "petals": "5", "edge": ["notched", "rounded square"], "size": "medium", "shape": "cluster", "tree": "no", "description": "What delightful stuff!", "new": false }, { "name": "Italian bellflower", "genus": "Campanula", "colors": ["white"], "petals": "5", "edge": ["pointy"], "size": "small", "shape": "bell", "tree": "no", "description": "This little crawling plant is also known as falling stars, trailing campanula, and star of Bethlehem.", "new": false }, { "name": "Hens & Chicks", "genus": "Sempervivum", "colors": ["pink"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "star", "tree": "no", "description": "Cobweb house-leek is perhaps the more interesting of this plant's common names. It's no doubt called that because its little furry leaf cluster things look like they're covered in cobwebs.", "new": false }, { "name": "rose campion", "genus": "Silene", "colors": ["pink"], "petals": "5", "edge": ["notched", "rounded square"], "size": "medium", "shape": "flat", "tree": "no", "description": "This plant's got some funky alternative names like dusty miller, mullein-pink, and bloody William (which perhaps is the opposite of sweet William).", "new": false }, { "name": "fireweed", "genus": "Chamaenerion", "colors": ["purple"], "petals": "5", "edge": ["rounded", "notched"], "size": "large", "shape": "stalk", "tree": "no", "description": "Fireweed looks nothing like fire! That name comes from how well this plant swoops in on sites that have just had forest fires and such. (For example, it's called bombweed in the UK because it came in after blitzes.) If you don't like thinking about fires or bombs you can call this willowherb.", "new": false }, { "name": "butterfly weed", "genus": "Asclepias", "colors": ["orange"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "cluster", "tree": "no", "description": "This is a type of milkweed! It's got a whole list of entertaining common names like orange Swallow-wort, windroot, and butterfly love.", "new": false }, { "name": "lavender", "genus": "Lavandula", "colors": ["purple"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "stalk", "tree": "no", "description": "What a lovely pollinator plant! You can distinguish this from Russian sage by its size (lavender is smaller) and leaves (long thin leaves instead of compound ones).", "new": false }, { "name": "alfalfa", "genus": "Medicago", "colors": ["purple"], "petals": "5", "edge": ["round", "fused"], "size": "small", "shape": "ball", "tree": "no", "description": "Fun fact about alfalfa: its name comes from an Old Persian phrase meaning \"horse food.\"", "new": false }, { "name": "Stansbury's cliffrose", "genus": "Purshia", "colors": ["white", "yellow"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "flat", "tree": "no", "description": "As you can see, a sci-fi monster overtakes these flowers in the summertime.", "new": false }, { "name": "Rocky Mountain beeplant", "genus": "Cleome", "colors": ["pink"], "petals": "4", "edge": ["round"], "size": "small", "shape": "cluster", "tree": "no", "description": "This plant is also known as Navajo spinach, guaco, skunk weed, stinking-clover, and bee spider-flower. I chose to go with \"Rocky Mountain beeplant\" since it seemed the least offensive (and also because I've got tons of spider-flowers on this site already\u2014maybe we can use octopus flower next?).", "new": false }, { "name": "creeping thistle", "genus": "Cirsium", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "ball", "tree": "no", "description": "Also known as Canada thistle, field thistle, and... lettuce from hell thistle.", "new": false }, { "name": "paintbrush", "genus": "Castilleja", "colors": ["red"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "stalk", "tree": "no", "description": "It's paintbrush! A perfectly named flower!", "new": false }, { "name": "bittersweet nightshade", "genus": "Solanum", "colors": ["purple"], "petals": "5", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "The berry of this plant is attractive but poisonous, which is perhaps why it's also referred to by names such as poisonflower, snakeberry, felonwort, and poisonberry. ", "new": false }, { "name": "katniss", "genus": "Sagittaria", "colors": ["white"], "petals": "3", "edge": ["rounded square", "notched"], "size": "medium", "shape": "flat", "tree": "no", "description": "I am a big fan of the Hunger Games so I'm disappointed that I didn't realize what this was at the time. It's also called wapato, Indian potato, and broadleaf arrowhead\u2014you can find it in many wet-ish-lands, and eat it, which Suzanne Collin totally gets right in her books.", "new": false }, { "name": "pepperweed", "genus": "Lepidium", "colors": ["white"], "petals": "4", "edge": ["round"], "size": "small", "shape": "cluster", "tree": "no", "description": "Pepperweed is my best guess for this one. It's a good guess though, right? ", "new": false }, { "name": "hardy blue-flowered leadwort", "genus": "Ceratostigma", "colors": ["blue"], "petals": "5", "edge": ["rounded square", "notched"], "size": "small", "shape": "flat", "tree": "no", "description": "Leadwort is brilliant and I think more flowers should be bright blue!", "new": false }, { "name": "St. John's wort", "genus": "Hypericum", "colors": ["yellow"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "star", "tree": "no", "description": "This is such a fun, ridiculous flower with lots of potential medicinal purposes. It's named after St. John because it tends to blossom around St. John's Feast Day, June 24.", "new": false }, { "name": "Strawberry Candy daylily", "genus": "Hemerocallis", "colors": ["pink"], "petals": "6", "edge": ["fused", "ruffled"], "size": "large", "shape": "trumpet", "tree": "no", "description": "What a frilly friend! This daylily, according to the internet, has a red eyezone. I didn't even know flowers had eyes.", "new": false }, { "name": "California fuschia", "genus": "Epilobium", "colors": ["red"], "petals": "4", "edge": ["notched", "round"], "size": "small", "shape": "trumpet", "tree": "no", "description": "I'm tempted to call this Zauschneria, which was its genus name before people figured it would fit better in Epilobium. It's also known by the fun titles firechalice and hummingbird trumpet.", "new": false }, { "name": "sunflower", "genus": "Helianthus", "colors": ["yellow"], "petals": "many", "edge": ["pointy", "notched"], "size": "large", "shape": "daisy-like", "tree": "no", "description": "Who doesn't like a good sunflower? ", "new": false }, { "name": "rubber rabbitbrush", "genus": "Ericameria", "colors": ["yellow"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "I'm pretty sure this is rubber rabbitbrush, also known as chamisa. It's called rabbitbrush because jackrabbits like to hide in it.", "new": false }, { "name": "shortstem buckwheat", "genus": "Eriogonum", "colors": ["yellow"], "petals": "many", "edge": ["round", "fused"], "size": "small", "shape": "cluster", "tree": "no", "description": "Gotta be honest\u2014it was kind of disappointing to go on a summer hike and only find yellow wildflowers.", "new": false }, { "name": "white heath aster", "genus": "Symphyotrichum", "colors": ["white"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "daisy-like", "tree": "no", "description": "This stuff's also called \"frost aster,\" which is a fun phrase to say fast.", "new": false }, { "name": "Nanho Purple butterfly bush", "genus": "Buddleia", "colors": ["pink", "purple"], "petals": "5", "edge": ["notched", "ruffled"], "size": "small", "shape": "stalk", "tree": "no", "description": "This looks absolutely nothing like butterfly weed but is somehow just as stunning.", "new": false }, { "name": "Russian sage", "genus": "Salvia", "colors": ["purple"], "petals": "5", "edge": ["round", "notched", "fused"], "size": "small", "shape": "stalk", "tree": "no", "description": "A friendly (but messy-looking) pollinator plant that looks much like lavender. It's got compound leaves and is slightly lighter in color.", "new": false }, { "name": "beeblossom", "genus": "Oenothera", "colors": ["white"], "petals": "4", "edge": ["pointy"], "size": "small", "shape": "star", "tree": "no", "description": "Also known as clockweed and white gaura, this plant has flowers that look more like butterflies than bees!", "new": false }, { "name": "purple coneflower", "genus": "Echinacea", "colors": ["pink"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "The centers of coneflowers are mesmerizing :O", "new": false }, { "name": "black-eyed Susan", "genus": "Rudbeckia", "colors": ["yellow"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "Poor Susan, I wonder what happened to her. (You can also call this flower poor-land daisy and yellow ox-eye daisy.)", "new": false }, { "name": "red hot poker", "genus": "Kniphofia", "colors": ["orange", "yellow"], "petals": "1", "edge": ["fused"], "size": "large", "shape": "stalk", "tree": "no", "description": "This friend, native to Africa, can also be referred to as tritoma, torch lily, and poker plant.", "new": false }, { "name": "pink geranium", "genus": "Geranium", "colors": ["pink"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "flat", "tree": "no", "description": "Sometimes geraniums come in clusters.", "new": false }, { "name": "Gussie Harris daylily", "genus": "Hemerocallis", "colors": ["yellow"], "petals": "6", "edge": ["ruffled", "round"], "size": "large", "shape": "trumpet", "tree": "no", "description": "What a striking flower! These daylilies are really enormous and fit right in with the light at sunset.", "new": false }, { "name": "Purple Prince zinnia", "genus": "Zinnia", "colors": ["pink"], "petals": "many", "edge": ["rounded square"], "size": "medium", "shape": "circle", "tree": "no", "description": "Turns out Zinnias are named after a man named Zinn!", "new": false }, { "name": "Milka gladiolus", "genus": "Gladiolus", "colors": ["purple"], "petals": "6", "edge": ["pointy", "ruffled"], "size": "large", "shape": "stalk", "tree": "no", "description": "Milka is fantastic and so is the Milka gladiolus.", "new": false }, { "name": "Purity cosmos", "genus": "Cosmos", "colors": ["white"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "bowl", "tree": "no", "description": "Some people on the internet think cosmos is saucer-shaped, and they're probably right, but I think a saucer without a cup is basically a bowl.", "new": false }, { "name": "Spartacus dahlia", "genus": "Dahlia", "colors": ["red"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "circle", "tree": "no", "description": "According to one website I found, the Spartacus dahlia \"adds drama.\"", "new": false }, { "name": "Sun Explosion dahlia", "genus": "Dahlia", "colors": ["orange", "yellow"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "circle", "tree": "no", "description": "This dahlia hasn't even bloomed yet!", "new": false }, { "name": "Peter Pears gladiolus", "genus": "Gladiolus", "colors": ["orange"], "petals": "6", "edge": ["pointy", "ruffled"], "size": "large", "shape": "stalk", "tree": "no", "description": "Peter Pears was an English singer whose last name sounds like \"Peers.\" I looked up a picture of him which does not resemble this flower at all.", "new": false }, { "name": "Caf\u00e9 au Lait Royal dahlia", "genus": "Dahlia", "colors": ["pink"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "circle", "tree": "no", "description": "Some dahlias are called \"dinner-plate\" dahlias because they are giant.", "new": false }, { "name": "Tampico gladiolus", "genus": "Gladiolus", "colors": ["pink"], "petals": "6", "edge": ["pointy", "ruffled"], "size": "large", "shape": "stalk", "tree": "no", "description": "Gladiolus is sometimes called sword lily!", "new": false }, { "name": "Pinkie cosmos", "genus": "Cosmos", "colors": ["pink"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "bowl", "tree": "no", "description": "Cosmos is a delight to find in a garden because it's tall and sort of like a cloud with lots of flowers facing every which way.", "new": false }, { "name": "red sunflower", "genus": "Helianthus", "colors": ["red"], "petals": "many", "edge": ["pointy", "notched"], "size": "large", "shape": "daisy-like", "tree": "no", "description": "It is so hard to tell what kind of sunflowers sunflowers are!", "new": false }, { "name": "white cornflower", "genus": "Centaurea", "colors": ["white"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "circle", "tree": "no", "description": "Cornflowers aren't always cornflower blue! I found these next to a cluster of pink ones.", "new": false }, { "name": "Mammoth sunflower", "genus": "Helianthus", "colors": ["yellow"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "daisy-like", "tree": "no", "description": "Maybe this sunflower wasn't a mammoth sunflower but it was at least as big as my head.", "new": false }, { "name": "borage", "genus": "Borago", "colors": ["blue", "purple"], "petals": "5", "edge": ["pointy"], "size": "small", "shape": "star", "tree": "no", "description": "What a friend! Also known as starflower, borage is pretty, edible, and extremely spiky. I knelt down in some to take this picture and boy were my legs not happy.", "new": false }, { "name": "Texas bluebell", "genus": "Eustoma", "colors": ["purple"], "petals": "many", "edge": ["round", "ruffled"], "size": "large", "shape": "cup", "tree": "no", "description": "Other names for this very fun giant purple flower are showy prairie gentian, prairie gentian, and Lisianthus.", "new": false }, { "name": "Autumn Beauty sunflower", "genus": "Helianthus", "colors": ["yellow", "red"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "daisy-like", "tree": "no", "description": "It's possible all the sunflowers on this site are the same variety and I'm just trying to show off how many sunflower color patterns I've seen.", "new": false }, { "name": "Velvet Queen sunflower", "genus": "Helianthus", "colors": ["red", "yellow"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "daisy-like", "tree": "no", "description": "Maybe it's not a Velvet Queen sunflower, but it certainly looks like it deserves the name Velvet Queen.", "new": false }, { "name": "scarlet sage", "genus": "Salvia", "colors": ["red"], "petals": "many", "edge": ["round", "pointy"], "size": "medium", "shape": "stalk", "tree": "no", "description": "This plant looks like it's sticking a pink tongue out at you!", "new": false }, { "name": "sulfur diablo cosmos", "genus": "Cosmos", "colors": ["orange"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "bowl", "tree": "no", "description": "These flowers can be used as a dye, which is pretty cool! Isn't that color magnificent?", "new": false }, { "name": "Pacifica Really Red vinca", "genus": "Vinca", "colors": ["red"], "petals": "5", "edge": ["rounded square", "pointy"], "size": "small", "shape": "flat", "tree": "no", "description": "This is the first non-periwinkle purple periwinkle I've seen! I think it's hilarious that it's called \"really red\" because that's exactly what it is.", "new": false }, { "name": "Big Kiss White Flame gazania", "genus": "Gazania", "colors": ["white", "pink"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "I'm surprised I hadn't heard of gazanias before I identified this one\u2014they're so lovely!", "new": false }, { "name": "Blushing Bride Rose of Sharon", "genus": "Hibiscus", "colors": ["white", "pink"], "petals": "5", "edge": ["ruffled"], "size": "large", "shape": "trumpet", "tree": "no", "description": "I'm not totally sure I identified this one correctly but I do know that it's a double-flowered hibiscus. Double-flowered flowers are super interesting because they're all mutated (most roses you know about are double-flowered).", "new": false }, { "name": "Crazytunia Frisky Purple petunia", "genus": "Petunia", "colors": ["white", "pink", "purple"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "trumpet", "tree": "no", "description": "Reading about this plant made me learn about the \"spiller thriller filler\" combination for plant containers.", "new": false }, { "name": "white globe amaranth", "genus": "Gomphrena", "colors": ["white"], "petals": "many", "edge": ["pointy", "notched"], "size": "small", "shape": "ball", "tree": "no", "description": "Just your classic globe amaranth, but white this time!", "new": false }, { "name": "Obsession Blue With Eye verbena", "genus": "Verbena", "colors": ["purple", "white"], "petals": "5", "edge": ["notched"], "size": "small", "shape": "cluster", "tree": "no", "description": "I hate to break it to whoever named this flower, but it isn't blue.", "new": false }, { "name": "bluebeard", "genus": "Caryopteris", "colors": ["blue", "purple"], "petals": "many", "edge": ["pointy", "round"], "size": "small", "shape": "stalk", "tree": "no", "description": "This flower looks super blue up close, but a nice light purple from a distance!", "new": false }, { "name": "Candystripe cosmos", "genus": "Cosmos", "colors": ["pink", "white"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "bowl", "tree": "no", "description": "What a delightful variety of cosmos!", "new": false }, { "name": "white hollyhock", "genus": "Alcea", "colors": ["white"], "petals": "many", "edge": ["ruffled", "notched"], "size": "large", "shape": "bowl", "tree": "no", "description": "A nice lady identified this one for me because she heard me wondering out loud.", "new": false }, { "name": "Radiance cosmos", "genus": "Cosmos", "colors": ["pink"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "bowl", "tree": "no", "description": "Cosmos just gets better and better.", "new": false }, { "name": "Chater's Double Pink hollyhock", "genus": "Alcea", "colors": ["pink"], "petals": "many", "edge": ["ruffled"], "size": "large", "shape": "rose-like", "tree": "no", "description": "I'm getting super good at identifying double-flowered flowers. This variety's even got a mention on the Wikipedia page for hollyhock!", "new": false }, { "name": "pink hollyhock", "genus": "Alcea", "colors": ["pink"], "petals": "many", "edge": ["ruffled"], "size": "large", "shape": "bowl", "tree": "no", "description": "A nice, pink hollyhock. You can use the stems of hollyhock for firewood if you want.", "new": false }, { "name": "mountain cornflower", "genus": "Centaurea", "colors": ["purple"], "petals": "many", "edge": ["pointy", "notched"], "size": "small", "shape": "circle", "tree": "no", "description": "The mountain cornflower, or the mountain bluet, is such a spiky iteration on a cornflower! It makes me realize that cornflowers are like softer thistles.", "new": false }, { "name": "Pink Potion spiked speedwell", "genus": "Veronica", "colors": ["pink"], "petals": "4", "edge": ["notched"], "size": "small", "shape": "stalk", "tree": "no", "description": "According to the internet this variety of veronica is part of a series of veronicas called \"Magic Show,\" which includes a plant called \"Wizard of Ahhs.\" ", "new": false }, { "name": "purple loosestrife", "genus": "Lythrum", "colors": ["purple", "pink"], "petals": "6", "edge": ["ruffled"], "size": "small", "shape": "stalk", "tree": "no", "description": "I actually cannot tell if these flowers are pink or purple, and it seems like their Wikipedia page is doing worse than me because it says they're \"reddish purple.\"", "new": false }, { "name": "lanceleaf tickseed", "genus": "Coreopsis", "colors": ["yellow"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "The notches on these leaves are just incredible.", "new": false }, { "name": "Macedonian scabious", "genus": "Knautia", "colors": ["pink"], "petals": "many", "edge": ["rounded square"], "size": "small", "shape": "circle", "tree": "no", "description": "Scabious is the other, less fun word for \"pincushion flower.\" This little flower has a green sweat bee on it!", "new": false }, { "name": "apricot mallow", "genus": "Sphaeralcea", "colors": ["orange"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "bowl", "tree": "no", "description": "What a happy plant! Its Wikipedia page lists it as a larval host for a bunch of butterflies with lovely names, like the common checkered skipper and West Coast lady.", "new": false }, { "name": "Little Princess Japanese anemone", "genus": "Anemone", "colors": ["pink"], "petals": "5", "edge": ["round", "ruffled"], "size": "medium", "shape": "bowl", "tree": "no", "description": "The fact that these have two colors is just fantastic. My neighbors have these and they are a delight to walk by.", "new": false }, { "name": "Alexandra bougainvillea", "genus": "Bougainvillea", "colors": ["purple"], "petals": "5", "edge": ["pointy", "round"], "size": "small", "shape": "star", "tree": "no", "description": "I'm being confusing by classifying the tiny white flowers and also the purple of the surrounding bracts. Bougainvilleas are native to South America!", "new": false }, { "name": "Corona SunPatiens", "genus": "Impatiens", "colors": ["orange"], "petals": "5", "edge": ["notched", "round"], "size": "medium", "shape": "flat", "tree": "no", "description": "Sunpatiens are a kind of impatiens that live in full sun, which is very impressive of them.", "new": false }, { "name": "Pixie Queen bougainvillea", "genus": "Bougainvillea", "colors": ["pink"], "petals": "5", "edge": ["pointy", "round"], "size": "small", "shape": "star", "tree": "no", "description": "This variety of bouganvillea is more of a shrub than a vine!", "new": false }, { "name": "Yellow King Humbert canna lily", "genus": "Canna", "colors": ["yellow"], "petals": "3", "edge": ["ruffled"], "size": "large", "shape": "trumpet", "tree": "no", "description": "I guess maybe this was named for Umberto I, the King of Italy for something like 12 years?", "new": false }, { "name": "Summerwings Dark Elegance begonia", "genus": "Begonia", "colors": ["orange"], "petals": "4", "edge": ["round"], "size": "medium", "shape": "cup", "tree": "no", "description": "Begonia is very silly and reminds me of a skinny bird. I think I've identified the correct variety of begonia this is, but I could certainly be wrong. People everywhere are just makin' begonias!", "new": false }, { "name": "garden stonecrop", "genus": "Hylotelephium", "colors": ["pink"], "petals": "5", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "This stuff is pink, pleasant, and succulent.", "new": false }, { "name": "sponge gourd", "genus": "Luffa", "colors": ["yellow"], "petals": "5", "edge": ["ruffled", "pointy"], "size": "large", "shape": "star", "tree": "no", "description": "Sponge gourd is called that because you can use its skeleton as a sponge. Funky!", "new": false }, { "name": "Hello Yellow blackberry lily", "genus": "Iris", "colors": ["yellow"], "petals": "6", "edge": ["round"], "size": "medium", "shape": "flat", "tree": "no", "description": "The blackberry lily, also known as the leopard flower, was reclassified as part of the Iris genus in 2005.", "new": false }, { "name": "snail vine", "genus": "Cochliasanthus", "colors": ["purple"], "petals": "1", "edge": ["ruffled"], "size": "medium", "shape": "snail", "tree": "no", "description": "Whatever this magnificent thing is, you can also call it corkscrew vine, snail creeper, snailflower, and snail bean.", "new": false }, { "name": "Blush Pink SunPatiens", "genus": "Impatiens", "colors": ["pink"], "petals": "5", "edge": ["notched", "round"], "size": "medium", "shape": "flat", "tree": "no", "description": "I noticed impatiens for the first time in a garden right after it had rained, which was perfect for their vibrant colors to stand out!", "new": false }, { "name": "ivy-leaved geranium", "genus": "Pelargonium", "colors": ["pink"], "petals": "5", "edge": ["round", "notched"], "size": "small", "shape": "flat", "tree": "no", "description": "This plant, also known as cascading geranium... isn't even a geranium, I think.", "new": false }, { "name": "meadow garlic", "genus": "Allium", "colors": ["white"], "petals": "6", "edge": ["pointy"], "size": "small", "shape": "ball", "tree": "no", "description": "Like many garlics, this plant has an edible bulb! It's also known as Canada onion and Canada garlic.", "new": false }, { "name": "sweet autumn clematis", "genus": "Clematis", "colors": ["white"], "petals": "4", "edge": ["pointy"], "size": "small", "shape": "star", "tree": "no", "description": "The seed clusters of this plant are super noodle-y and fun! I encourage you to look them up.", "new": false }, { "name": "chocolate cosmos", "genus": "Cosmos", "colors": ["brown"], "petals": "many", "edge": ["rounded square"], "size": "small", "shape": "bowl", "tree": "no", "description": "Chocolate cosmos smells like chocolate :)", "new": false }, { "name": "southern globethistle", "genus": "Echinops", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "ball", "tree": "no", "description": "This flower isn't too poky if you touch it. ", "new": false }, { "name": "lily of the Incas", "genus": "Alstroemeria", "colors": ["pink"], "petals": "6", "edge": ["pointy", "notched"], "size": "medium", "shape": "trumpet", "tree": "no", "description": "I couldn't figure out exactly which kind of lily of the Incas, or Peruvian lily, this is\u2014it's still super pleasant, though.", "new": false }, { "name": "gentian sage", "genus": "Salvia", "colors": ["blue"], "petals": "2", "edge": ["fused", "notched", "round"], "size": "small", "shape": "funky", "tree": "no", "description": "Man oh man! The color of this flower gets a kick out of me.", "new": false }, { "name": "Japanese anemone", "genus": "Anemone", "colors": ["pink"], "petals": "many", "edge": ["round"], "size": "medium", "shape": "bowl", "tree": "no", "description": "Anemones are also known as windflowers because they have long stems that let them sway in the wind!", "new": false }, { "name": "licorice mint", "genus": "Agastache", "colors": ["pink", "orange", "purple"], "petals": "3", "edge": ["fused"], "size": "small", "shape": "stalk", "tree": "no", "description": "Also known as threadleaf giant hyssop and Mexican Hyssop, this plant is quite attractive to hummingbirds.", "new": false }, { "name": "Moonraker Cape fuschia", "genus": "Phygelius", "colors": ["yellow"], "petals": "5", "edge": ["fused"], "size": "medium", "shape": "cluster", "tree": "no", "description": "Also called Cape figwort, these plants are neither closely related to true fuschias or figworts.", "new": false }, { "name": "Leavenworth's eryngo", "genus": "Eryngium", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "medium", "shape": "danger", "tree": "no", "description": "This thing is NOT a thistle.", "new": false }, { "name": "Mango Popsicle red hot poker", "genus": "Kniphofia", "colors": ["orange"], "petals": "1", "edge": ["fused"], "size": "large", "shape": "stalk", "tree": "no", "description": "What a nice color! Really makes me want a mango popsicle.", "new": false }, { "name": "Purple tiger rose", "genus": "Rosa", "colors": ["pink"], "petals": "many", "edge": ["pointy", "ruffled"], "size": "large", "shape": "rose-like", "tree": "no", "description": "This rose is just ridiculous. Who taught it to do that? ", "new": true }, { "name": "double-flower African violet", "genus": "Saintpaulia", "colors": ["purple"], "petals": "many", "edge": ["ruffled"], "size": "small", "shape": "rose-like", "tree": "no", "description": "Tiny and cute! If you look closely you can see little hairs on the petals of this one.", "new": true }, { "name": "African violet", "genus": "Primulina", "colors": ["purple"], "petals": "5", "edge": ["fused", "round"], "size": "small", "shape": "trumpet", "tree": "no", "description": "This flower looks like it would talk to you if it could.", "new": true }, { "name": "Autumn Sunburst Encore Azalea", "genus": "Rhododendron", "colors": ["pink", "white"], "petals": "5", "edge": ["fused", "round"], "size": "medium", "shape": "trumpet", "tree": "no", "description": "I love that the people who name flowers give them such delectable names. Autumn sunburst! Mmm!", "new": true }, { "name": "anise-scented sage", "genus": "Salvia", "colors": ["purple"], "petals": "2", "edge": ["fused", "round"], "size": "small", "shape": "funky", "tree": "no", "description": "Also called hummingbird sage, this plant is native to South America.", "new": true }, { "name": "silverleafed princess flower", "genus": "Pleroma", "colors": ["purple"], "petals": "5", "edge": ["notched", "rounded square"], "size": "small", "shape": "flat", "tree": "no", "description": "I've got nothing to add. :)", "new": true }, { "name": "firecracker flower", "genus": "Crossandra", "colors": ["orange"], "petals": "5", "edge": ["rounded", "notched", "fused"], "size": "small", "shape": "bag", "tree": "no", "description": "This plant is called firecracker flower because its seed pods explode. So there's that.", "new": true }, { "name": "pagoda flower", "genus": "Clerodendrum", "colors": ["white"], "petals": "5", "edge": ["pointy"], "size": "small", "shape": "star", "tree": "no", "description": "This plant is native to tropical Asia as well as Papuasia, which is a botanical region, which is something defined by the World Geographical Scheme for Recording Plant Distributions (WGSRPD), something I did not know existed until a few moments ago.", "new": true }, { "name": "Chocolate Sunrise canna lily", "genus": "Canna", "colors": ["yellow"], "petals": "3", "edge": ["ruffled"], "size": "large", "shape": "trumpet", "tree": "no", "description": "I'm trying really hard to get my canna lilies right, but nobody's got a dichotomous key for those! What's up with that? ", "new": true }, { "name": "King of Siam tropical water lily", "genus": "Nymphaea", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "waterlily", "tree": "no", "description": "This variety of water lily has only been around since 1998!", "new": true }, { "name": "Hot Pink tropical water lily", "genus": "Nymphaea", "colors": ["pink"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "waterlily", "tree": "no", "description": "I don't actually know what this water lily's name is, but it is hot pink.", "new": true }, { "name": "Cape Blue water lily", "genus": "Nymphaea", "colors": ["purple"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "waterlily", "tree": "no", "description": "What a perfect flower :)", "new": true }, { "name": "Pygmy water lily", "genus": "Nymphaea", "colors": ["white"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "waterlily", "tree": "no", "description": "Water lilies are the best!", "new": true }, { "name": "white pitcherplant", "genus": "Sarracenia", "colors": ["white"], "petals": "1", "edge": ["ruffled"], "size": "large", "shape": "pitcher", "tree": "no", "description": "This plant eats bugs and you can too! (If you want.)", "new": true }, { "name": "pickerelweed", "genus": "Pontederia", "colors": ["blue"], "petals": "6", "edge": ["round", "pointy"], "size": "medium", "shape": "stalk", "tree": "no", "description": "If you look closely you can see a little bee on this one!", "new": true }, { "name": "prairie blazing star", "genus": "Liatris", "colors": ["pink"], "petals": "5", "edge": ["pointy"], "size": "large", "shape": "stalk", "tree": "no", "description": "These things are very tall and also known as cattail blazing star and cattail gayfeather.", "new": true }, { "name": "Little Henry", "genus": "Rudbeckia", "colors": ["yellow"], "petals": "many", "edge": ["fused", "notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "Little Henry is a type of coneflower with petals that look like french fries. I find everything about that fantastic.", "new": true }, { "name": "clasping heliotrope", "genus": "Heliotropium", "colors": ["purple"], "petals": "5", "edge": ["round", "fused"], "size": "small", "shape": "cluster", "tree": "no", "description": "Also known as blue heliotrope and summer heliotrope, this plant is spreading too much in Australia so people are trying to control its population with beetles.", "new": true }, { "name": "purpledisk honeycombhead", "genus": "Balduina", "colors": ["yellow"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "This guy could give Little Henry a run for its money in terms of having petals that look like french fries.", "new": true }, { "name": "episcea lilacina", "genus": "Episcea", "colors": ["purple"], "petals": "5", "edge": ["notched"], "size": "medium", "shape": "flat", "tree": "no", "description": "A finely-textured friend.", "new": true }, { "name": "guzmania conifera", "genus": "Guzmania", "colors": ["red", "yellow"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "cone", "tree": "no", "description": "A spiky friend!", "new": true }, { "name": "macleania", "genus": "Macleania", "colors": ["red"], "petals": "5", "edge": ["pointy", "fused"], "size": "small", "shape": "cluster", "tree": "no", "description": "For some reason these remind me of candy corn.", "new": true }, { "name": "Low's pitcher plant", "genus": "Nepenthes", "colors": ["purple"], "petals": "1", "edge": ["pointy"], "size": "large", "shape": "pitcher", "tree": "no", "description": "These were discovered on Mount Kinabalu, the highest mountain in Borneo and Malaysia.", "new": true }, { "name": "ceratostema silvicola", "genus": "Ceratostema", "colors": ["red", "purple"], "petals": "5", "edge": ["pointy", "fused"], "size": "small", "shape": "cluster", "tree": "no", "description": "These are way too waxy to not be candy, right? ", "new": true }, { "name": "firecracker bush", "genus": "Bouvardia", "colors": ["red"], "petals": "4", "edge": ["pointy", "fused"], "size": "small", "shape": "cluster", "tree": "no", "description": "The Wikipedia page for these flowers say that they are \"spectacular\" and I could not agree more.", "new": true }, { "name": "lollipop plant", "genus": "Pachystachys", "colors": ["yellow", "white"], "petals": "2", "edge": ["fused"], "size": "medium", "shape": "stalk", "tree": "no", "description": "I can't decide which common name I like better for this plant: lollipop plant or golden shrimp plant.", "new": true }, { "name": "queen lily ginger", "genus": "Curcuma", "colors": ["pink"], "petals": "many", "edge": ["round", "fused"], "size": "large", "shape": "stalk", "tree": "no", "description": "This beautiful thing is also called jewel of Thailand and hidden ginger.", "new": true }, { "name": "desert rose", "genus": "Adenium", "colors": ["pink"], "petals": "5", "edge": ["ruffled"], "size": "medium", "shape": "star", "tree": "no", "description": "A beautiful, periwinkle-y flower, the desert rose also goes by names including mock azalea, impala lily, and kudu.", "new": true }, { "name": "Arkansas ironweed", "genus": "Vernonia", "colors": ["pink", "purple"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "ball", "tree": "no", "description": "Perhaps ironweed's called that because it's bitter to mammals.", "new": true }, { "name": "Mystic Illusion dahlia", "genus": "Dahlia", "colors": ["yellow"], "petals": "many", "edge": ["pointy", "notched"], "size": "large", "shape": "daisy-like", "tree": "no", "description": "I'm not sure what the illusion is supposed to be\u2014I just think these dahlias are cute.", "new": true }, { "name": "butterfly bush", "genus": "Rotheca", "colors": ["blue"], "petals": "5", "edge": ["rounded square", "round"], "size": "small", "shape": "star", "tree": "no", "description": "This plant has many medicinal uses, including treating diabetes.", "new": true }, { "name": "white garland lily", "genus": "Hedychium", "colors": ["white"], "petals": "4", "edge": ["ruffled"], "size": "large", "shape": "cluster", "tree": "no", "description": "Also known as white ginger, butterfly lily, and fragrant garland flower, this is the national flower of Cuba.", "new": true }, { "name": "Purple Formosa Azalea", "genus": "Rhododendron", "colors": ["pink"], "petals": "5", "edge": ["ruffled"], "size": "large", "shape": "flat", "tree": "no", "description": "Azaleas are always a pleasant find on a suburb walk.", "new": true }, { "name": "Titan Polka Dot periwinkle", "genus": "Vinca", "colors": ["white"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "flat", "tree": "no", "description": "It still blows my mind that not all periwinkles are purple for some reason.", "new": true }, { "name": "Southern Fried lantana", "genus": "Lantana", "colors": ["orange"], "petals": "4", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "I'm not sure if this is Southern Fried lantana, but I sure hope it is because that name is rockin'.", "new": true }, { "name": "Madagascar periwinkle (1)", "genus": "Vinca", "colors": ["pink"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "flat", "tree": "no", "description": "These are a really good garden box or roadside pot flower.", "new": true }, { "name": "angelonia", "genus": "Angelonia", "colors": ["white"], "petals": "5", "edge": ["rounded square"], "size": "small", "shape": "stalk", "tree": "no", "description": "What impressive blooms! They're sorta like snapdragons in their shape.", "new": true }, { "name": "pee gee hydrangea", "genus": "Hydrangea", "colors": ["white"], "petals": "4", "edge": ["round"], "size": "large", "shape": "cluster", "tree": "no", "description": "This might be my hot take, but I like the blue-or-pink hydrangeas best.", "new": true }, { "name": "wax begonia", "genus": "Begonia", "colors": ["pink"], "petals": "4", "edge": ["pointy", "notched"], "size": "medium", "shape": "bowl", "tree": "no", "description": "The waxiness of these petals is quite pleasing to the eye.", "new": true }, { "name": "Madagascar periwinkle (2)", "genus": "Vinca", "colors": ["pink"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "flat", "tree": "no", "description": "This is the first time I'm posting two pictures of the same plant! I just liked the difference in the flower center colors and wanted to show it off.", "new": true }, { "name": "Tropical Sunrise canna lily", "genus": "Canna", "colors": ["orange"], "petals": "3", "edge": ["ruffled"], "size": "large", "shape": "trumpet", "tree": "no", "description": "Once again I am just trying my best to have identified this canna.", "new": true }, { "name": "Autumn Twist Encore azalea", "genus": "Rhododendron", "colors": ["pink"], "petals": "5", "edge": ["notched", "ruffled"], "size": "large", "shape": "flat", "tree": "no", "description": "This is a particularly weird Autumn Twist Encore azalea! Most of the time they just have some dark pink stripes, but this one mutated all funky.", "new": true }, { "name": "Peach Drift rose", "genus": "Rosa", "colors": ["pink"], "petals": "many", "edge": ["ruffled"], "size": "medium", "shape": "rose-like", "tree": "no", "description": "Lately I have been trying to stop and smell the roses. 10/10 worth it.", "new": true }, { "name": "crepe myrtle", "genus": "Lagerstroemia", "colors": ["pink"], "petals": "many", "edge": ["ruffled"], "size": "medium", "shape": "cluster", "tree": "yes", "description": "These flowers are named for their crepe-like texture.", "new": true }, { "name": "Miss Oklahoma canna lily", "genus": "Canna", "colors": ["pink"], "petals": "3", "edge": ["ruffled"], "size": "large", "shape": "trumpet", "tree": "no", "description": "I'm not sure if I got this canna right but I sure like that name so I'm keeping it here until someone corrects me.", "new": true }, { "name": "lilyturf", "genus": "Liriope", "colors": ["purple"], "petals": "5", "edge": ["round"], "size": "small", "shape": "stalk", "tree": "no", "description": "Also called border grass and monkey grass.", "new": true }, { "name": "sulfur cosmos", "genus": "Cosmos", "colors": ["orange"], "petals": "many", "edge": ["notched"], "size": "medium", "shape": "bowl", "tree": "no", "description": "Shortly after I took this picture, a gulf fritillary landed on this flower, resulting in the best picture I have taken in my whole life. That's not the one I'm including here because I don't want you to get distracted from the flower.", "new": true }, { "name": "mums", "genus": "Chrysanthemum", "colors": ["pink"], "petals": "many", "edge": ["notched", "round"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "Mums and chrysanthemums are the same! Use that next time you get chrysanthemum in a spelling bee.", "new": true }, { "name": "purpletop vervain", "genus": "Verbena", "colors": ["purple"], "petals": "5", "edge": ["notched", "rounded square"], "size": "small", "shape": "cluster", "tree": "no", "description": "Also called clustertop vervain, tall verbena and pretty verbena.", "new": true }, { "name": "Magenta Magic dahlia", "genus": "Dahlia", "colors": ["pink"], "petals": "many", "edge": ["ruffled"], "size": "large", "shape": "daisy-like", "tree": "no", "description": "I like that someone, probably the person who grew it, named this \"magic.\" Like, whoa, I can't believe I grew that!", "new": true }, { "name": "Starship Deep Rose lobelia", "genus": "Lobelia", "colors": ["pink"], "petals": "5", "edge": ["pointy"], "size": "medium", "shape": "stalk", "tree": "no", "description": "It's time to start using sci-fi names for flowers, apparently. You heard it here first.", "new": true }, { "name": "Heartland Pink Sunset lantana", "genus": "Lantana", "colors": ["pink"], "petals": "4", "edge": ["pointy"], "size": "small", "shape": "cluster", "tree": "no", "description": "Everyone's just out here making new kinds of lantana.", "new": true }, { "name": "Orange Wonder snapdragon", "genus": "Antirrhinum", "colors": ["orange"], "petals": "5", "edge": ["round", "fused"], "size": "large", "shape": "stalk", "tree": "no", "description": "Snapdragons are also called toadflax (so are a few other genuses, so don't get confused) and dog flower.", "new": true }, { "name": "black-eyed Susan vine", "genus": "Thunbergia", "colors": ["orange"], "petals": "5", "edge": ["notched", "rounded square"], "size": "medium", "shape": "flat", "tree": "no", "description": "Remember sweet William? He was allegedly the lover of black-eyed Susan in a song! It's flower name lore!", "new": true }, { "name": "Mexican flamevine", "genus": "Pseudogynoxys", "colors": ["red"], "petals": "many", "edge": ["pointy", "notched"], "size": "medium", "shape": "daisy-like", "tree": "no", "description": "The old scientific name for this flower translated to \"confused old man\" in Latin, because if you don't give the plant something to climb on, it gets all scraggly.", "new": true }, { "name": "cactus dahlia", "genus": "Dahlia", "colors": ["pink"], "petals": "many", "edge": ["pointy", "fused"], "size": "large", "shape": "circle", "tree": "no", "description": "I'm pretty sure cactus dahlias are ones with these wild poky petals.", "new": true }, { "name": "Kahili ginger", "genus": "Hedychium", "colors": ["yellow", "orange"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "stalk", "tree": "no", "description": "Something I'm finding out as I go to more tropical exhibits is that gingers are weird.", "new": true }, { "name": "red powder puff", "genus": "Calliandra", "colors": ["red"], "petals": "many", "edge": ["pointy"], "size": "small", "shape": "flat", "tree": "yes", "description": "A fabulous little flower!", "new": true }, { "name": "Bieberstein's crocus", "genus": "Crocus", "colors": ["purple"], "petals": "6", "edge": ["pointy"], "size": "medium", "shape": "cup", "tree": "no", "description": "In lots of places, the way you can tell that it's spring is crocuses.", "new": true }, { "name": "azure blue sage", "genus": "Salvia", "colors": ["blue"], "petals": "3", "edge": ["fused", "rounded square"], "size": "medium", "shape": "stalk", "tree": "no", "description": "This plant is native to much of the US.", "new": true }, { "name": "monkshood", "genus": "Aconitum", "colors": ["blue"], "petals": "5", "edge": ["fused", "round"], "size": "large", "shape": "stalk", "tree": "no", "description": "Also called aconite, wolfsbane, leopard's bane, devil's helmet, and blue rocket\u2014a plethora of fantastic names for a very poisonous plant.", "new": true }, { "name": "Apricot Desire dahlia", "genus": "Dahlia", "colors": ["orange"], "petals": "many", "edge": ["pointy"], "size": "large", "shape": "circle", "tree": "no", "description": "I feel like peach is a rare and underrated flower color.", "new": true }, { "name": "Mirage Hot Pink sage", "genus": "Salvia", "colors": ["pink"], "petals": "3", "edge": ["fused", "round"], "size": "medium", "shape": "funky", "tree": "no", "description": "You can tell it's sage because I don't know what to call the flower shape at all.", "new": true }, { "name": "elephant head amaranth", "genus": "Amaranthus", "colors": ["purple"], "petals": "many", "edge": ["ruffled"], "size": "large", "shape": "ball", "tree": "no", "description": "I find these flowers really uncomfortable. It's like automatic topiary.", "new": true }];